.disable{
  display: none;
  color:#7f7f93;
}
.display{
  opacity:0!important;
}
.account-permiss__content{
  box-sizing: border-box;
  padding: 0px 20px;
  background: #fff;
  width: 97%;
  margin: 0 auto;
}
.account-permiss{
  font-size: 14px;
  line-height: 25px;

  &__search{
    box-sizing: border-box;
   
    h3{
      margin: 0px;
      font-size: 16px;
      color:#000;
      display: block;
      font-weight: 600;
      padding:15px 0px 10px;
    }
    input{
      font-size: 13px;
      outline: none;
      max-width: 306px;
      &::placeholder{
        color:#7f7f93;
      }
    }
    select{
      font-size: 13px;
      margin-left: 15px;
      width: 155px;
      color:#000;
      
      }
    input[type=submit]{
      margin-left: 15px;
      line-height: 30px;
      background: #f4f4f4;
      width: 155px;
      border:1px solid #ddd;
      border-radius: 4px;
      color:#444;
    }
    
  }
  &__list{
    display: table;
    border-spacing: 15px;
    .col-sm-4,.col-sm-8{
     display: table-cell;
     float: none;
     border:2px solid #000;     
    }
    h3{
      font-size: 20px;
      color:#7f7f93;
      font-weight: 600;
    }
    .table1{
      margin: 0px -15px;
        table{
          // border:2px solid #000;
          width: 100%;
          table-layout: fixed;
           tr{
            border-width: 0px 0px 1px 0px;
             border-color: #f4f4f4;
              border-style: solid;                         
             &:nth-child(1){
               font-weight: 700;
              border-width: 0px 0px 1px 0px;  
              color:#000;             
             }
             &:nth-child(11){
                border-width: 0px 0px 0px 0px;
                td{
                  text-align: right;
                  border-right: 0px!important;
                }
                  ul{
                    display: inline-block;
                    border:1px solid #f4f4f4;
                    border-radius: 4px;
                    font-size: 0;
                    overflow: hidden;
                    li{
                      padding: 0px;
                      line-height: 25px;
                      a{
                        font-size: 14px;
                        display: block;
                        padding: 0px 10px;
                        border-right: 1px solid #f4f4f4;
                        
                          &:nth-child(3){
                            border:none;
                          }
                          &:hover{
                            background: #337ab7;
                            color:#fff;
                            border-color:#337ab7;
                            transition: 0.3s ease-in-out;
                          }
                      }
                    }
                  }
              }
           }
           td{
              padding: 7px 10px;
              border-right: 1px solid #f4f4f4;             
              &:nth-child(4){
                border-width: 0px;
              }
              &:nth-child(1){
               width: 50px;
               text-align: center;
              }
              &:nth-child(2){
                width: 150px;
               text-align: left;
              }
              &:nth-child(3){
                width: 200px;
                text-align: left;
              }
           }
        }
    }
    .form-check{
      box-sizing: border-box;
      padding:0px 20px 0px; 
        &__item{
          margin-bottom: 7px;
          line-height: 30px;
          input{
            margin: 0px;
            vertical-align: middle;
            margin-right: 20px;
          }
          label{
            vertical-align: middle;
            font-weight: 400;
            color:#7f7f7f;
            font-weight: 600;
            cursor: pointer;
              i{
                color:#7f7f7f;
                font-weight: 300;
                font-size: 10px;
              }
          }
        }
    }
  }
  &__title{
    h3{
      font-size: 20px;
      color:#7f7f93;
      font-weight: 600;
    }
  }
}
/************/
.account-permiss__btn{
  padding:30px 0px 20px;
  text-align: center;
  margin: 0 auto;
  a{
    display: block;
    line-height: 35px;
    color:#fff;
      &:hover{
        background: #337ab7;
      }
  }
  button{
    display: inline-block;
    line-height: 35px;
    color: #fff;
    border: none;
    background: #3c8dbc;
    margin: 0px;
    width: 100%;
    text-align: center;
    &:hover{
      background: #337ab7;
    }
  }
    .save-btn{
      display: inline-block;
      font-weight: 600;
      width: 50px;
      text-align: center;
      background: #3c8dbc;
      margin-right: 40px;
      border-radius: 5px;
      overflow: hidden;
    }
    .cancel-btn{
      display: inline-block;
      font-weight: 600;
      width: 110px;
      text-align: center;  
      background: #3c8dbc;
        border-radius: 5px;
        overflow: hidden;
    }
}
/***************/
.edit-template{
  font-size: 14px;
  line-height: 25px;
  label{
    color:#7f7f7f;
    width: 25%;
    text-align: right;
    box-sizing: border-box;
    padding-right:80px; 
    float:left;
    margin-bottom: 0px;
    line-height: 34px;
  }
  input{
    float: left;
  }
  textarea{
    resize: none;
    float: left;
  }
  select{
    float: left;
    width: 190px;
  }
  .edit-input{
    display: block;
    margin-bottom: 40px;
      &:nth-child(1){
        padding-top: 45px;
          input{
            width: 215px;
          }
        }
     
    
  }
}
/**************/
.equipment{
  padding-bottom: 50px;
  font-size: 14px;
  line-height: 25px;
  &__content{
    padding-bottom: 50px;
  }
  h4{
    padding: 20px 0px 35px;
    margin: 0px;
    color:#000;
    font-size: 16px;
    font-weight: 600;
  }
  &__content--input{
    box-sizing: border-box;
    padding-left: 25px;
    display: block;
    text-align: left;
    margin-bottom: 38px;
    label{
      font-weight: 600;
      width: 105px;
      line-height: 34px;
      color:#000;
    }
    input{
      width: 190px;
    }
    select{
      width: 190px;
    }
    textarea{
      width: 100%;
      resize:none;
    }
  }
  .search-room{
    input{
      width: 300px;
      margin-right: 15px;
    }
    select{
      width: 155px;
      margin-right: 15px;
    }
    button{
      line-height: 30px;
      outline: none;
      width: 155px;
      border: none;
      background: #f4f4f4;
      border-radius: 5px;
      border:1px solid #d2d6de;
    }
  }
  .table-room{
    margin-top: 25px;
    width: 100%;
      table{
        width: 100%;
        table-layout: fixed;
        .btn-info{
          border-color:transparent;
        }
      
      }
      tr{
          th{
            text-align: center;
            border:1px solid #ddd;
            &:nth-child(1){
              width: 38px;
              text-align: center;
              
            }
            &:nth-child(2){
              width: 190px;
              text-align: center;
            }
            &:nth-child(3){
              width: 190px;
              text-align: center;
            }

            &:nth-child(4){
              width: 190px;
              text-align: center;
            }
          }
          &:last-child{
            box-sizing: border-box;
            
            td{
              text-align: right!important;
              padding: 10px 20px 10px 0px;

            }
              ul{
                
                vertical-align: middle;
                display: inline-block;
                border:1px solid #f4f4f4;
                border-radius: 4px;
                font-size: 0;
                overflow: hidden;
                li{
                  padding: 0px;
                  line-height: 25px;
                  a{
                    font-size: 14px;
                    display: block;
                    padding: 0px 10px;
                    border-right: 1px solid #f4f4f4;
                    
                      &:nth-child(3){
                        border:none;
                      }
                      &:hover{
                        background: #337ab7;
                        color:#fff;
                        border-color:#337ab7;
                        transition: 0.3s ease-in-out;
                      }
                  }
                }
              }
          }
        td{
          border: 1px solid #ddd;
          &:first-child{
            width: 38px;
            text-align: center;
          }

          &:nth-child(2){
            font-weight: 700;
            text-align: left;
            box-sizing: border-box;
            padding:10px 10px 10px 15px;
            width: 190px;                     
          }
          &:nth-child(3){
            text-align: left;
            box-sizing: border-box;
            padding:10px 10px 10px 15px;   
            width: 190px;         
          }
          &:nth-child(4){
            text-align: left;
            box-sizing: border-box;
            padding:10px 10px 10px 15px; 
            width: 190px;           
          }
          &:nth-child(5){
            text-align: center;  
              i{
                cursor: pointer;
                padding: 5px 8px;
                border-radius: 4px;
                background: #dd4b39;
                color:#fff;
              }      
          }
        }
      }
      
  }
}