
  .topbar{
    box-sizing: border-box;
    padding: 20px 30px;
    color:#7f7f93;
    font-size: 14px;
    line-height: 35px;
    
     h2{
       font-weight: 600;
       font-size: 24px;
       margin: 0px;
       display: inline-block;
       vertical-align: middle;
     }
    ul{
      vertical-align: middle;
     
        a{
          display: block;
        }
    }
  }
