.list-account{
  padding-top: 15px;
  padding: 30px 30px;
  .table>thead:first-child>tr:first-child>th{
    border-top: 1px solid gray;
  }
  .table>thead>tr>th,
  .table>tbody>tr>th,
  .table>tfoot>tr>th,
  .table>thead>tr>td, 
  .table>tbody>tr>td,
  .table>tfoot>tr>td{
    border: 1px solid gray;
    border: 1px solid gray;
  }
  .content-top{
    button.btn{
      margin-top: 25px;
      width: 100%;
    }
  }
  .table-content{
    tr.table-hide.active{
      display: table-row;
    }
    tr.table-hide{
      display: none;
      width: 100%;
      background: #fff;
      .drop-menu-table{
        border-top: 3px solid #F39C12;
        border-radius: 5px;
        padding: 15px;
        .title{
          h3{
            margin: 0;
            padding-bottom: 10px;
            border-bottom: 1px solid #eee;
          }
        }
        .row{
          .row{
            padding-top: 15px;
            padding-bottom: 15px;
          }
          p{
            margin-top: 6px;
            margin-bottom: 0;
          }
          button{
            background:#3C8DBC;
            margin-right: 30px;
            margin-top: 30px;
            &:hover{
              background: #fff;
              border: 1px solid gray;
              color: #000;
            }
          }
        }
      }
    }
    button{
      background:#00A65A;
      border: 1px solid transparent;
      border-radius: 3px;
      transition: all 0.3s;
      outline: none;
      i{
        color: white;
      }
      &:hover{
        background: #fff;
        border: 1px solid gray;
        i{
          color: #000;
        }
      }
    }
  }
  .page-nav{
    a{
      &:hover{
        background-color: cornflowerblue;
        color: #fff;
      }
    }
  }
}
.add-account{
  padding-top: 50px;
  .row{
    padding-bottom: 40px;
    margin: 0;
    p{
      margin-top: 6px;
    }
    button{
      margin: 15px;
    }
  }
}
.Statistical{
  .title{
    h4{
    margin: 0;
    padding-bottom: 10px;
    }
    h3{
      border: none;
    }
    border-bottom: 1px solid #eee;
    margin-bottom: 15px;
  }
  &__list{
    ul{
      border-spacing: 0;
      border-collapse: collapse;
      display: table-row;
      &.title{
        p{
          font-weight: 600;
        }
      }
      &.time{
        color: gray;
        li{
          &:first-child{
            color: #333;
          }
        }
      }
    }
    li{
      &:first-child{
        font-weight: 600;
      }
      border: 1px solid #eee;
      display: table-cell;
      p{
        padding: 5px 15px;
      }
    }
  }
}